import React, {Component} from 'react';
import "./School.css";
const CosmosClient = require("@azure/cosmos").CosmosClient;
const config = require("../../config");

const { endpoint, key, databaseId, containerId } = config;

const client = new CosmosClient({ endpoint, key });

const database = client.database(databaseId);
const container = database.container(containerId);


function delay(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
  }

class DataInput2 extends Component{
    constructor(){

        super();
        this.state =
        {data:'',
        data2:'',
        status:'Safe'
      };

        this.componentDidMount = this.componentDidMount.bind(this) 
    }

    async componentDidMount() {
        // const response = await fetch(`https://api.coinmarketcap.com/v1/ticker/?limit=10`);
        const querySpec = {
            query: "SELECT top 1 * FROM c ORDER BY c._ts DESC"
            
          };

          
          // read all items in the Items container
          const { resources: items } = await container.items
            .query(querySpec)
            .fetchAll();


            items.forEach(item =>{
                // console.log(this.state.data)
                // console.log(item.peoplecount)
                if(item.hasOwnProperty("peoplecount") && item.hasOwnProperty("banana")){
                    if(this.state.data.value !==item.peoplecount){
                        this.setState({
                            data:item.peoplecount
                        }, console.log('peoplecount',this.state.data))
                    }
                    if(this.state.data2.value !==item.banana){
                      this.setState({
                          data2:item.banana
                      }, console.log('banana',this.state.data2))
                  }
                    return true;
                }

            })

            if(parseInt(this.state.data2) !== 0){
              this.setState({
                    status:"Danger"
              }, console.log('status',this.state.status))
            }else{
              this.setState({
                status:"Safe"
          }, console.log('status',this.state.status))

            }


            await delay(1000);
            await this.componentDidMount();
        setInterval(this.componentDidMount(),200);
        
      }

      render(){


        console.log("return1", this.state.data)
        console.log("return2", this.state.data2)
        console.log("return3", this.state.status)
        return(
        this.state.status ==='Danger'?
          <div className="alertIcon">
          <svg width="903" height="625" viewBox="0 0 997 691" overflow="hidden" fill="none" xmlns="http://www.w3.org/2000/svg">
          <rect x="0.05" y="0.05" width="999.9" height="695.9" stroke="black" stroke-width="0.1"/>
          <path d="M633.026 395.513C633.026 398.006 631.006 400.026 628.513 400.026C626.021 400.026 624 398.006 624 395.513C624 393.021 626.021 391 628.513 391C631.006 391 633.026 393.021 633.026 395.513Z" fill="#FA041A"/>
          <path d="M627.686 392.41H629.34V396.15H627.686V392.41Z" fill="white"/>
          <path d="M627.686 396.72H629.34V398.265H627.686V396.72Z" fill="white"/>
          </svg>
          </div>
        :<div></div>
        )

      }
     }
export default DataInput2;
