import React from "react";
import "./FunctionBarwithMap.css";
import Zone from "./zone";



class FunctionBarwithMap extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            isToggleOn: true,selectionMode:"",
            selectionFloor:"f1"


        };
        this.handleClick = this.handleClick.bind(this);
        this.modeChange = this.modeChange.bind(this);
        this.floorChange = this.floorChange.bind(this);

    }


    handleClick() {
        this.setState(prevState => ({
          isToggleOn: !prevState.isToggleOn
        }));
      }

    modeChange(event){
    console.log(event.target.value);
    this.setState({
        selectionMode:event.target.value
    })
}
    floorChange(event){
        console.log("floor",event.target.value);
        this.setState({
            selectionFloor:event.target.value
        })
    }

    render(){
        return(
            <div className="rightColumn">
                <div className="tools">
                    <div className="functionMenu" onChange={this.modeChange}>

                    <input id="exitEdit" type="radio" name="mode" value = ""/><label className="mode" for="exitEdit" >Pure Map</label>

                        <input id="ICSleadpoint" type="radio" name="mode" value = "ICSleadpoint"/> <label className="mode" for="ICSleadpoint">ICS lead point</label>

                        <input id="Casualtycollectionpoint" type="radio" name="mode" value = "Casualtycollectionpoint"/><label className="mode" for="Casualtycollectionpoint" >Casualty collection point</label>

                        <input id="ZoneColor" type="radio" name="mode" value = "ZoneColor"/><label className="mode" for="ZoneColor" >Zone color</label>


                    </div>
                    <div className="icslead"> <strong>ICS lead: </strong> {this.state.isToggleOn ? 'N/A ' : 'John Doe '}
                    <button className="buttonICS" type="button" onClick={this.handleClick}>{this.state.isToggleOn ? 'Assign ' : 'Switch '} </button></div>
                </div>
                <div className="mapwindow">
                    <Zone selectionMode = {this.state.selectionMode} floorChange={this.state.selectionFloor}/>
                
                <div className="floorSwitch" onChange={this.floorChange}>
                    <input id="f1" type="radio" name="floor" value = "f1" defaultChecked={true}/> <label className="floor" for="f1">F1</label>
                    <input id="f2" type="radio" name="floor" value = "f2"/><label className="floor" for="f2" >F2</label>
{/* 
                    <input id="f3" type="radio" name="floor" value = "f3"/><label className="floor" for="f3" >F3</label>
                    
                    <input id="f4" type="radio" name="floor" value = "f4"/><label className="floor" for="f4" >F4</label> */}
                </div>
                </div>
            </div>
        )
    }
}

export default FunctionBarwithMap;